@tailwind base;
@tailwind components;
@tailwind utilities;


@mixin my-btn() {
  padding: .6rem;
  height: auto;
  Text-Transform: none !important;
  font-weight: normal !important;
  font-style: normal !important;
  border-radius: 4px !important;

}

@mixin my-animation-block() {
  transition: 1s;
  opacity: 1;
}


$my_height: 54px;
$my-blue: #5096FE;
$my-hover-blue: #397CE1;
$my-orange: #ED6E13;
$my-hover-orange: #E65D0D;
$color-background: #EBEFF3;
html {
  background-color: $color-background !important;
  overflow: hidden;
}

.input {
  &:focus {
    outline-offset: 0;
    outline: 0;
  }
}

.btn {
  @include my-btn;

  &-authorization {
    display: flex;
    justify-content: space-between;
  }

  &-list {
    justify-content: left;

    &:hover {
      color: #282c34;
    }
  }

  &-search {
    height: 54px;
    margin-left: 10px;
  }

  &-calendar {
    font-size: 1.5rem;
    color: $my-orange;
  }

  &-stopwatch {
    font-size: 1.5rem;
    color: #B9BDC1;
  }

  &-delete {
    float: right;

    &:hover {
      background: none;
    }
  }

  &-close-modal {
    color: $my-blue !important;
  }

  &-primary {
    background-color: $my-blue;
    border-color: $my-blue;
    color: white;
    font-weight: bold !important;

    &:hover {
      background-color: $my-hover-blue;
      border-color: $my-hover-blue;
    }

    &-outline {
      background-color: white;
      border-color: $my-blue;
      color: $my-blue;

      &:hover {
        background-color: $my-hover-blue;
        border-color: $my-hover-blue;
        color: white;
      }
    }
  }

  &-warning {
    background-color: $my-orange;
    border-color: $my-orange;
    &:hover{
      background-color: $my-hover-orange;
      border-color: $my-hover-orange;
      color: white;
    }
  }
}


.icon {
  &-exit {
    transform: rotate(180deg);
    color: rgba(0, 0, 0, 0.5);
    margin: 0;
    padding: 0;
  }

  &-plus {
    color: white;
  }

  &-delete {
    font-size: 24px;
    color: #B9BDC1;

    &:hover {
      color: #6E7C91;

    }
  }

  &-close-modal {
    font-size: 24px;
    color: #5096FE !important;
  }
}

.textarea-ghost, .textarea:focus {
  outline: 0 !important;
}
.dropdown {
  &-content {
    padding: 1rem;
  }
}

.toggle-warning:checked, .toggle-warning[checked="true"], .toggle-warning[aria-checked=true] {
  background-color: $my-orange;
}

.checkbox {
  margin: 1rem;
  border-color: #B9BDC1;
}