@import "src/core/components/App/App";

.header-app {
  &-title {
    text-align: center;
    margin-bottom: 3rem;
  }
}

.avatar {
  margin-right: .4rem;
}

.drawer {
  &-content {
    overflow: hidden !important;
    padding: 0;
    margin: 0;
  }

  &-side {
    z-index: 1 !important;

    &-category-title {
      color: rgba(35, 36, 37, 0.3);
      font-style: normal;
      font-weight: 700;
      font-size: 15px;
      line-height: 120%;
    }
  }
}

.drawer-content::-webkit-scrollbar {
  width: 10px;
  background-color: rgba(248, 0, 0, 0);
  z-index: 3 !important;
}

.drawer-content::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #606060;
}

.circle {
  clip-path: circle(50%);
  height: 1em;
  width: 1em;
}

.blackout {
  background-color: rgba(40, 44, 52, 0.5) !important;
}