@import "src/core/components/App/App";
.custom-card {

  padding-left: 4px;
  display: flex;
  flex-direction: row;
  margin-top: .5rem;
  border: 1px solid rgb(255, 255, 255) !important;
  border-radius: 8px;
  align-items: center;

  @include my-animation-block;
  &:hover {
    border: 1px solid #6E7C91!important;
    transition: .5s;
  }

  &-pre {
    display: flex;
    align-items: center;
    border-radius: 7px;

    &-text {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 100%;
    }
  }

}

