@import "../../../../../core/components/App/App";

.task-content {
  width: 100%;

  &-header {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: space-between;
    border-bottom: 1px solid $color-background;

    &-part {
      &-one {
        display: flex;
        align-items: center;

        &-checkbox {
          border-right: 1px solid $color-background;
          margin-right: 1rem;
        }
      }

      &-two {
        display: flex;
        align-items: center;

        &-date {
          margin-right: 2rem;
          font-size: 1.125rem;
        }
      }
    }
  }

  &-body {

    min-height: 80vh;
    max-height: 80vh;

    &-title {
      font-size: 1.125rem;
      font-weight: bold;
      display: block !important;
      word-wrap: break-word;
      margin-bottom: .5rem;
    }

    &-text {
      font-size: 0.938rem;

      &-textarea {
        height: 67vh;
        resize: none;
      }
    }
  }

  &-footer {
    display: flex;
    justify-content: space-between;
    border-top: 1px solid $color-background;
    padding: 1.375rem;
    min-height: 5.75rem;
    align-items: center;

    &-timers {
      margin: 0 1rem;
      font-size: 2rem;
      font-weight: bold;
      color: $my-orange;
    }

    &-btn {
      margin-right: 1rem;
    }
  }
}

