.modal-exit {
  vertical-align: middle;

  &-box {
    max-width: 70% !important;

    margin: 2rem auto;

    &-btn {
      display: grid;
      grid-gap: 10px;
      grid-template-columns: 1fr 1fr;

      &-item {
        width: 100%;
      }
    }

    &-text {
      font-size: 1.27rem;
      font-weight: bold;
      margin-bottom: 1rem;
    }
  }


}