@import '../../../../core/components/App/App';

$padding-home: 2rem;

.home {
  //@include my-animation-block;
  width: 100% !important;
  display: flex;
  height: 100vh;
  background-color: $color-background !important;
  animation-name: move;
  animation: move 1s;
  &-search-form {
    display: flex;

    &-btn {
      height: $my_height;
      width: $my_height;
      margin-right: 8px;
      font-size: 24px;

      &:hover {
        border-color: $my-orange;
        background-color: $my-orange;
      }
    }

    &-input {

      height: $my_height;
    }
  }

  &-task {
    @include my-animation-block;
    overflow: auto;

    &-list {
      @include my-animation-block;
      width: 50%;
      padding: $padding-home 1rem;

      &-active {
        @include my-animation-block;
        width: 100%;
        padding: $padding-home 16.688rem;
      }
    }

    &-content {
      //@include my-animation-block;
      max-width: 50%;
      width: 50%;
      padding: $padding-home 1rem;
      background-color: white;
      border-bottom-left-radius: 16px;
      border-top-left-radius: 16px;
      //animation-name: move;
      //animation: move 1s;

      &-active {
        @include my-animation-block;
        display: none;
      }
    }
  }
}

@keyframes move {
  0% {
    width: 0;
  }
  100% {
    width: 50%;
  }
}
